import React from 'react';
import { desktopHowerWrapperStyle } from './hover-wrapper.style';
import { THowerWrapperProps } from './prop.type';

export function HoverWrapperDesktop({ children, onHover }: THowerWrapperProps): JSX.Element {
  const handleMouseEnter = React.useCallback(() => onHover('enter'), [onHover]);
  const handleMouseLeave = React.useCallback(() => onHover('leave'), [onHover]);

  return (
    <div
      className={desktopHowerWrapperStyle}
      onMouseEnter={handleMouseEnter}
      onMouseLeave={handleMouseLeave}
    >
      {children}
    </div>
  );
}
